.section-about-fact {

    @include section-padding;

    @media screen and (min-width: $w1200) {
        padding-top: 130px;
    }



    & .container {
        @media screen and (min-width: $w1200) {
            // max-width: 1305px;
        }
    }
}

.about-fact {

    max-width: 1245px;
    margin: 0 auto;



    @media screen and (min-width: $w760) {
        display: flex;
        flex-wrap: wrap;
    }

    &__title {
        font-weight: 700;
        font-size: 40px;
        line-height: 111%;
        color: $blue;

        @media screen and (min-width: $w760) {
            font-size: 56px;
        }
    }

    &__subtitle {
        line-height: 157%;
        text-transform: uppercase;
    }

    &__intro,
    &__first,
    &__two,
    &__three,
    &__four,
    &__five,
    &__six {
        padding: 30px;
        background-color: $blue-for-card;
        min-height: 228px;
    }

    &__img {
        display: none;

        @media screen and (min-width: $w1200) {
            display: block;
            flex-basis: 287px;
            flex-grow: 1;

            & img {
                width: 100%;
                object-fit: cover;
            }
        }
    }

    &__middle {



        @media screen and (min-width: $w760) {
            //display: none;

            flex-basis: 480px;
            flex-grow: 2;
            order: 10;
        }



        @media screen and (min-width: $w1376) {
            display: block;
            flex-basis: 667px;
            flex-grow: 2;
        }

        @media screen and (min-width: $w1200) {
            display: block;
            flex-basis: 480px;
            flex-grow: 2;
        }


        & picture {
            width: 100%;
            height: 100%;
            display: flex;
            height: 228px;
        }

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: 62% center;
        }
    }

    &__number {
        padding-bottom: 20px;
        font-size: 16px;
        font-weight: 600;
    }

    &__desc {
        font-size: 16px;
        line-height: 150%;
        font-weight: 600;
    }



    &__intro {
        background-color: transparent;

        @media screen and (max-width: $w480) {
            height: auto;
            min-height: auto;
            padding-left: 0;
            padding-right: 0;

            & br {
                display: none;
            }
        }






        @media screen and (min-width: $w1376) {
            padding-left: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-basis: 550px;
        }

        @media screen and (min-width: $w1200) {
            flex-basis: 480px;
            flex-grow: 2;
            padding-left: 0;
            padding-top: 0;
            display: flex;
            justify-content: flex-start;
            align-items: center;

        }

        @media screen and (min-width: $w760) {
            width: 100%;
        }


    }



    &__first {
        background-color: $green;
        color: #fff;


        background-position: 104% 110%;
        background-repeat: no-repeat;

        @media screen and (min-width: $w1200) {
            flex-basis: 287px;
            flex-grow: 1;
        }

        @media screen and (min-width: $w1376) {}

    }

    &__two {
        @media screen and (min-width: $w1200) {
            flex-basis: 365px;
            flex-grow: 1;

        }
    }

    &__three {
        background-color: $blue;
        color: #fff;

        @media screen and (min-width: $w1200) {
            flex-basis: 287px;
            flex-grow: 1;
        }
    }

    &__four {
        background-color: $blue;
        color: #fff;

        background-position: top left;
        background-repeat: no-repeat;


        @media screen and (min-width: $w1200) {
            flex-basis: 287px;
            flex-grow: 1;
        }
    }

    &__five {
        background-color: $green;
        color: #fff;

        background-position: top 100%;
        background-repeat: no-repeat;

        @media screen and (min-width: $w1200) {
            display: block;
            flex-basis: 558px;
            flex-grow: 2;
            background-position: top right;
        }
    }

    &__six {
        @media screen and (min-width: $w1200) {
            display: block;
            flex-basis: 395px;
            flex-grow: 1;
        }
    }



    &__first,
    &__two,
    &__three,
    &__four,
    &__five,
    &__six {

        @media screen and (min-width: $w760) {
            width: 50%;
        }

        @media screen and (min-width: $w992) {
            width: 33.33%;
        }
    }


}